* {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }
.container {
    margin: 0 5em 0 5em; }
.header {

    margin: 5em 5em 0 12em; }

.header__menu {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    font-family: 'IBM Plex Sans', serif;
    padding: 0.2em 0 2em 0;
    font-size: 20px;
    text-transform: uppercase;
    li {
        display: inline;
        padding: 0.2em 0 0.2em 0;
        font-size: 20px;
        text-transform: uppercase;
        list-style: none;

        a {
            text-decoration: none;
            color: #2b2b2b;
            padding: 0.2em 1.5em 0.2em 1.5em; }
        a:hover:not(.last a) {
            border: 1px solid black;
            border-radius: 50px; } }



    .header__menu-last {
        border: 1px solid grey;
        border-radius: 50px; } }
.header__logo {
    justify-content: start; }
.header__logo-title {
    font-size: 35px;
    font-weight: bold;
    color: #2b2b2b; }

.header__logo-discription {
    font-size: 10px;
    font-weight: bold;
    color: #2b2b2b;
    text-transform: uppercase;
    letter-spacing: .3em; }

.header__logo-image {
    width: 5em;
    height: 5em;
    // margin: 3em 0 1em 10em
    border: 1px solid black;
    border-radius: 50%;
    background: #2b2b2b; }

.main__about-section {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 80px; }

.main__about {
    width: 70%;
    // height: 50%
    border-left: 5px solid #2b2b2b;
    padding: 30px 0 30px 100px; }


.main__title {
    display: flex;
    width: 80%;
    font-size: 90px;
    text-transform: uppercase;
    font-weight: bold;
    color: #2b2b2b;
    padding-bottom: 30px; }

.main__description {
    width: 80%;
    font-size: 20px; }

.main__copyrite {
    writing-mode: tb-rl;
    transform: rotate(180deg);
    font-size: 12px;
    font-weight: bold;
    margin-right: 20px; }

.main__social {
    margin-left: 7em;
    li {
        display: inline;
        list-style: none;
        padding-right: 25px;
        font-size: 25px; } }

.main__scrollbutton {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    i {
        font-size: 30px;
        margin-top: -10px; } }

.table {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 200px;
    background: linear-gradient(to top, #f3f3f3 50%, #ffffff 50%); }
.table__img-first {
    margin-top: 4em; }
.table__about-table {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 20%;
    background-color: #2b2b2b;
    z-index: 1;
    padding: 40px;
    margin-left: 7em;
    a {
        // text-decoration: none
        text-transform: uppercase;
        color: #eceaea;
        letter-spacing: 1px;
        font-size: 12px;
        font-weight: bold;
        padding-top: 15px; } }

.table__table-title {
    width: 50%;
    color: white;
    padding-bottom: 20px;
    font-size: 30px;
    font-weight: bold;
    text-transform: capitalize; }
.table__table-description {
    color: #6f6e6e;
    padding-bottom: 10px; }
.product {
    background:  100% 50%  / 50% 70%  no-repeat url("/public/img/bg-product.jpg"), no-repeat linear-gradient(to top,  #ffffff 15%, #ececec 15%,  #f4f4f4 90%);
    padding-top: 100px; }
.product__product {
    display: flex;
    flex-direction: column;
    margin-top: 200px; }
.product__about {
    // display: flex
    // flex-direction: column
    margin-left: 12em;
    width: 28%;
    a {
        color:  #2b2b2b;
        text-transform: uppercase;
        font-weight: bold;
        padding-top: 20px; } }
.product__title {

    p {
        width: 70%;
        color: #2b2b2b;
        font-size: 30px;
        font-weight: bold;
        text-transform: capitalize;
        padding-bottom: 15px; } }
.product__description {
    padding-bottom: 10px;
    color: #6f6e6e;
    p {
        padding-bottom: 10px;
        color: #2b2b2b; } }
.product__image {
    display: flex;
    margin-left: 5em;
    margin-top: 100px; }
.product__shop {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding-bottom: 10px; }
.product__shop-description {
    display: flex;
    width: 25%;
    justify-content: flex-end;
    font-size: 18px;
    color: #6f6e6e; }

.product__shop-link {
    display: flex;
    justify-content: flex-end;
    a {
        border: 2px solid #6f6e6e;
        border-radius: 50px;
        padding: 10px 60px 10px 60px;
        font-size: 30px;
        text-decoration: none;
        color: #6f6e6e;
        margin-left: 30px;
        vertical-align: middle; }
    i {
        padding-left: 10px;
        font-size: 20px; } }
.footer {
    margin: 7em 5em 5em 5em; }
.footer__menu {
    display: flex;
    width: 35%;
    justify-content: space-between;
    a {
        text-decoration: none;
        color: #6f6e6e; }
    ul {
        list-style: none;
        padding-right: 10px;
        font-size: 14px;
        li {
            padding-bottom: 3px; }
        li:first-child {
            font-weight: bold;
            padding-bottom: 25px;
            font-size: 15px;
            color: #6f6e6e; } } }

.footer__copyrite {
    display: flex;
    justify-content: flex-end;
    color: #6f6e6e; }
